  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 29px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.paginationActive a {
  color: rgba(12, 134, 186, var(--tw-text-opacity));
  border: 1px solid rgba(12, 134, 186, var(--tw-text-opacity));
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 7px;
